.skills {
  min-height: 100vh;
  background-color: #252427;
  display: flex;
  justify-content: center;
  padding: 100px 0;

}

.container {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

div.blockTitle{
  min-height: 30px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center
}

.headerTitle{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.headerTitle h2{
  font-size: 40px;
}

.line{
  height: 2px;
  width: 55px;
  background-color: #6b236b;
}




